@import url(https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap);
* {
    box-sizing: border-box;
 }
 
 body {
     margin: 0;
     padding: 0;
     background-color: lightblue;
 }






nav {
    display: flex;
    background-color: #2a5be2;
    color: white;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;
    height: 4rem;
}
 
h3 {
    font-size: 3.3rem;
    align-items: center;
    margin: .1rem; 
    font-family: 'Fredoka One', cursive;
}

.nav-link {
    display: flex;
}

.nav-links {
    font-size: 1.3rem;
    list-style: none;
    text-decoration: none;
    margin: .5rem;
    padding: .5rem;
}

.nav-links:hover {
    cursor: pointer;
}

.nav-links-icons {
    font-size: .9rem;
}

.link {
    text-decoration: none;
    color: white;
    border-radius: 10px;
    transition: all 0.4s ease-out;
    cursor: pointer;
}

.toggle-button {
    position: absolute;
    top: .8rem;
    right: .7rem;
    color: white;
    display: none;
    font-size: 2.3rem;
}

.toggle-button:hover {
    cursor: pointer;
}

.nav-menu {
    display: none;
}

.toggle-link {
    color: coral;
}

.toggle-link:hover {
    cursor: pointer;
}

.toggle-link:focus {
    outline: none;
}

@media (max-width: 635px) {
    nav {
        display: flex;
    }

    h3 {
        display: flex;
        position: absolute;
        left: 5px;
        font-size: 2rem;
    }

    .toggle-button {
        display: flex;
        font-size: 1.85rem;
        right: 1rem;
        top: 1rem;
    }

    .nav-link {
        display: none;
    }

    .nav-menu-div {
        position: absolute;
        margin-top: 2rem;
        width: 100%;
        height: 40rem;
        background-color: #013220;
        z-index: 1;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        margin-top: 3.5rem;
        margin-left: 2.5rem;
        width: 100%;
    }

    .nav-menu-links {
        padding: 1.5rem;
        font-size: 2.6rem;
    }

    .toggle-link-icons {
        font-size: 2rem;
    }
}






.home-text {
   position: absolute;
   top: 35%;
   right: 1%;
   width: 50%;
   text-align: center;
   font-size: 1.5rem;
}

.home-icons {
    position: relative;
    font-size: 3rem;
    width: 10%;
    top: 24rem;
    left: 50%;
    margin-top: 1rem;
}

.home-icons:hover {
    cursor: pointer;
}

.home-image {
    position: absolute;
    width: 44%;
    height: 24rem;
    background-image: url('https://www.goodnewsfinland.com/wp-content/uploads/2016/04/Finternet_group.jpg');
    background-size: cover;
    margin-top: 5.5rem;
    margin-left: 2.1rem;
}

.home-image:hover {
    cursor: pointer;
    background-image: url('https://media.graphcms.com/Cx3t70EQF2aeqKiUTVbl');
    transform: scale(1.05);
    transition: all .4s ease-in-out;
}

@media (max-width: 635px) {
    .home-image {
        margin-top: 2rem;
        width: 88%;
        height: 12rem;
    }
    .home-text {
        font-size: 1rem;
        margin-right: 6rem;
        margin-top: 7rem;
        text-align: center;
        width: 50%;
    }
    .home-icons {
        margin-top: 4rem;
        left: 25%;
        font-size: 2rem;
        top: 24rem;
    }
}

* {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    text-decoration: none;
    list-style-type: none;
    outline: none;
  }
.container {
    display: flex;
    margin-top: 2rem;
    font-size: 1.3rem;
}

label {
    font-weight: bold;
    display: flex;
    margin-bottom: .5rem;
}

input + label {
    display: inline-flex;
    margin-right: .8rem;
}

input[type='fullName'],
input[type='email'],
textarea {
  display: block;
  width: 18rem;
  padding: 6px 12px;
  font-size: 1rem;
  line-height: 1.4;
  color: #555;
  background-color: #fff;
  border: 1px solid #ccc;
}

input[type='fullName']:focus,
input[type='email']:focus,
textarea:focus {
    outline: none;
}

.form-control {
    margin-bottom: 3rem;
}

.error {
    color: red;
}

.date {
    border: none;
    width: 10rem;
    line-height: 2;
}

.date:focus {
    outline: none;
    cursor: pointer;
}

.radio-label {
    font-weight: lighter;
    font-size: 1.1rem;
}

button[type='submit'] {
    border: none;
    width: 6rem;
    height: 3rem;
    margin-bottom: 3rem; 
    font-size: 1rem;
    font-weight: bold;
    border-radius: .8rem;
}

button[type='submit']:hover {
    cursor: pointer;
}

button[type='submit']:focus {
    outline: none;
}

@media (max-width: 635px) {
    .container {
        font-size: .8rem;
    }

    input[type='fullName'],
    input[type='email'],
    textarea {
        width: 12rem;
    } 

    .date {
        width: 8rem;
    }

    .form-control {
        margin-bottom: 2rem;
    }

    button[type='submit'] {
        width: 4rem;
    }
    .radio-label {
        font-size: .9rem;
    }
}
.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.todo-form {
    display: flex;
    flex-direction: row;
    align-items: center;
}

@media (max-width: 635px) {
    .container {
        margin: 2rem;
    }
}
